'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { 
  getCategories, 
  addCategory, 
  updateCategory, 
  deleteCategory,
  type Category 
} from '@/lib/utils'

export default function CategoriesPage() {
  const [categories, setCategories] = useState<Category[]>([])
  const [isEditing, setIsEditing] = useState(false)
  const [editingCategory, setEditingCategory] = useState<Category | null>(null)
  const [formData, setFormData] = useState({ name: '', description: '' })
  const [errors, setErrors] = useState<{ name?: string }>({})
  const [alert, setAlert] = useState<{ type: 'success' | 'error', message: string } | null>(null)

  useEffect(() => {
    loadCategories()
  }, [])

  const loadCategories = () => {
    try {
      const categoriesData = getCategories()
      setCategories(categoriesData)
    } catch (error) {
      showAlert('error', 'Gagal memuat data kategori')
    }
  }

  const validateForm = (): boolean => {
    const newErrors: { name?: string } = {}

    if (!formData.name.trim()) {
      newErrors.name = 'Nama kategori harus diisi'
    } else if (categories.some(cat => 
      cat.name.toLowerCase() === formData.name.toLowerCase().trim() && 
      cat.id !== editingCategory?.id
    )) {
      newErrors.name = 'Kategori dengan nama ini sudah ada'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return

    try {
      if (editingCategory) {
        // Update existing category
        const updatedCategory = updateCategory(editingCategory.id, {
          name: formData.name.trim(),
          description: formData.description.trim() || undefined
        })
        
        if (updatedCategory) {
          showAlert('success', 'Kategori berhasil diperbarui')
          resetForm()
          loadCategories()
        } else {
          showAlert('error', 'Gagal memperbarui kategori')
        }
      } else {
        // Add new category
        const newCategory = addCategory({
          name: formData.name.trim(),
          description: formData.description.trim() || undefined
        })
        
        if (newCategory) {
          showAlert('success', 'Kategori berhasil ditambahkan')
          resetForm()
          loadCategories()
        } else {
          showAlert('error', 'Gagal menambahkan kategori')
        }
      }
    } catch (error) {
      showAlert('error', 'Terjadi kesalahan saat menyimpan kategori')
    }
  }

  const handleEdit = (category: Category) => {
    setEditingCategory(category)
    setFormData({
      name: category.name,
      description: category.description || ''
    })
    setIsEditing(true)
  }

  const handleDelete = (id: string) => {
    if (!confirm('Apakah Anda yakin ingin menghapus kategori ini? Aset yang terkait mungkin akan terpengaruh.')) return

    try {
      const success = deleteCategory(id)
      if (success) {
        showAlert('success', 'Kategori berhasil dihapus')
        loadCategories()
      } else {
        showAlert('error', 'Gagal menghapus kategori')
      }
    } catch (error) {
      showAlert('error', 'Terjadi kesalahan saat menghapus kategori')
    }
  }

  const resetForm = () => {
    setFormData({ name: '', description: '' })
    setErrors({})
    setEditingCategory(null)
    setIsEditing(false)
  }

  const showAlert = (type: 'success' | 'error', message: string) => {
    setAlert({ type, message })
    setTimeout(() => setAlert(null), 5000)
  }

  return (
    <div className="space-y-6">
      {/* Alert */}
      {alert && (
        <Alert className={alert.type === 'error' ? 'border-red-200 bg-red-50' : 'border-green-200 bg-green-50'}>
          <AlertDescription className={alert.type === 'error' ? 'text-red-800' : 'text-green-800'}>
            {alert.message}
          </AlertDescription>
        </Alert>
      )}

      {/* Page Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Manajemen Kategori</h1>
          <p className="text-gray-600 mt-2">
            Kelola kategori untuk mengorganisir aset Puskesmas Kresek
          </p>
        </div>
      </div>

      {/* Add/Edit Form */}
      <Card>
        <CardHeader>
          <CardTitle>
            {isEditing ? 'Edit Kategori' : 'Tambah Kategori Baru'}
          </CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <label htmlFor="name" className="text-sm font-medium">
                  Nama Kategori *
                </label>
                <Input
                  id="name"
                  value={formData.name}
                  onChange={(e) => {
                    setFormData(prev => ({ ...prev, name: e.target.value }))
                    if (errors.name) setErrors({})
                  }}
                  placeholder="Masukkan nama kategori"
                  className={errors.name ? 'border-red-500' : ''}
                />
                {errors.name && (
                  <p className="text-sm text-red-600">{errors.name}</p>
                )}
              </div>

              <div className="space-y-2">
                <label htmlFor="description" className="text-sm font-medium">
                  Deskripsi
                </label>
                <Input
                  id="description"
                  value={formData.description}
                  onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
                  placeholder="Deskripsi kategori (opsional)"
                />
              </div>
            </div>

            <div className="flex gap-2 pt-2">
              <Button type="submit">
                {isEditing ? 'Perbarui Kategori' : 'Tambah Kategori'}
              </Button>
              {isEditing && (
                <Button type="button" variant="outline" onClick={resetForm}>
                  Batal
                </Button>
              )}
            </div>
          </form>
        </CardContent>
      </Card>

      {/* Categories List */}
      <Card>
        <CardHeader>
          <CardTitle>
            Daftar Kategori ({categories.length} kategori)
          </CardTitle>
        </CardHeader>
        <CardContent>
          {categories.length === 0 ? (
            <div className="text-center py-8 text-gray-500">
              <p className="text-lg mb-2">Belum ada kategori</p>
              <p className="text-sm">
                Mulai dengan menambahkan kategori pertama Anda
              </p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Nama Kategori</TableHead>
                    <TableHead>Deskripsi</TableHead>
                    <TableHead>Jumlah Aset</TableHead>
                    <TableHead>Aksi</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {categories.map((category) => {
                    const assetCount = getCategories().filter(cat => cat.id === category.id).length
                    return (
                      <TableRow key={category.id}>
                        <TableCell className="font-medium">{category.name}</TableCell>
                        <TableCell>
                          {category.description || '-'}
                        </TableCell>
                        <TableCell>
                          <span className="px-2 py-1 bg-gray-100 rounded-full text-sm">
                            {assetCount} aset
                          </span>
                        </TableCell>
                        <TableCell>
                          <div className="flex gap-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => handleEdit(category)}
                            >
                              Edit
                            </Button>
                            <Button
                              size="sm"
                              variant="destructive"
                              onClick={() => handleDelete(category.id)}
                            >
                              Hapus
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    )
                  })}
                </TableBody>
              </Table>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
